import React, { FC } from 'react';
import ImageGallery from 'react-image-gallery';
import {Row, Col} from 'reactstrap';

interface Props{
    init: any
}

const Gallery:FC<Props> = ({init}) =>{
    const photos = init.photos;
    return (
        <Row className="bm-5">
            <Col>
            <ImageGallery items={photos}/>
            </Col>
        </Row>
    )
}

export default Gallery